<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";

export default {
  data() {
    return {
      introView: false,
    };
  },
  methods: {
    introShow() {
      this.introView = !this.introView;
    },
    ...mapActions(["resize"]),
  },

  computed: {
    ...mapState(["db"]),
  },

  created() {
    this.resize();
  },

  mounted() {
    this.db.forEach((item) => {
      const parent = document.querySelector(`#${item.el}`);
      let observer = new IntersectionObserver((entries) => {
        entries.forEach((entry, index) => {
          if (entry.isIntersecting) {
            const el = parent.querySelectorAll(".animation");
            el.forEach((i) => {
              let sp = i.getAttribute("data-speed");
              i.classList.add("animate__animated");
              i.style.transition = sp + "ms";
            });
          }
        });
      }, item.zone);

      observer.observe(parent);
    });
  },
};
</script>

