<template>
  <transition name="modal">
    <div class="modal" v-show="modal" @click="$emit('closeModal', false)">
        <div 
        class="modal__block animate__animated"  
        :class="{animate__pulse: modalError}"
        @click.stop="" 
        >
          <p 
          class="modal__block-error" 
          :class="{active: modalError}"
          >
            Iltmos tekshirib qaytadan tering!
          </p>
          <p class="modal__block-text">Ba'tafsil ma'lumot olish uchun</p>
          <h3 class="modal__block-title">Raqamingizni qoldiring</h3>
          <form class="modal__contact" @submit.prevent="sendMessage" method="POST">
            <label>
              <input type="text" v-model="name" />
              <span class="modal__contact-span">Ismingiz</span>
            </label>
            <label>
              <input id="modalNum" type="tel" v-model="number" />
              <span class="modal__contact-span">Telefon raqam</span>
            </label>

            <button 
              type="submit" 
              class="modal__contact-btn">
              Yuborish
            </button>
          </form>

          <button
            class="modal__block-close"
            @click="$emit('closeModal', false)"
          >
            <img src="@/assets/Image/close-icon.svg" alt="" />
          </button>
        </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    modal: Boolean,
  },
  data() {
    return {
      name: "",
      number: "+998",
      modalError: false,
    };
  },
  methods: {
    sendMessage() {
      let splitNum = this.number.split('');
      let counterNum = 0;
      splitNum.forEach(a => {
        if(a.match(/[0-9]/)) {
          counterNum++
        }
      })
      if(this.name != '' && counterNum == 12) {
        const item = {
          name: this.name,
          num: this.number,
          date: new Date().toLocaleDateString(),
          time: new Date().toLocaleTimeString()
        };
        this.$emit('addNote', item)
        this.name = "",
        this.number = "+998"
      }else {
        this.modalError = true
        setTimeout(() => this.modalError = false, 2000)
      }
    }
  }
};
</script>

