import { createStore } from 'vuex'

const store = createStore({
    state: {
        openNav: false,
        size: null,
        db: [
            { el: 'mainIntro', zone: { threshold: 0 } },
            { el: 'mainServices', zone: { threshold: 0.6 } },
            { el: 'mainHistory', zone: { threshold: 0.6 } },
            { el: 'mainInfo', zone: { threshold: 0.6 } },
        ]
    },
    mutations: {
        resize(state, payload) {
            if (payload < 690 || window.innerWidth < 690) {
                state.db[1].zone['threshold'] = 0.3
            } else {
                state.db[1].zone['threshold'] = 0.6
            }
            state.size = payload
        },
        
        addClass(state, payload) {
            state.openNav = payload
        }
    },
    actions: {
        resize({ commit }) {
            commit('resize', window.innerWidth)
            window.addEventListener('resize', () => {
                let size = window.innerWidth;
                commit('resize', size)
            })
        }
    },
    getters: {}
})

export default store
