<template>
  <header class="header">
    <nav class="header__nav">
      <router-link to="/" class="logo">
        <img src="@/assets/Image/logo.png" alt="" />
      </router-link>
      <button 
      class="header__btn" 
      @click="(openNav = !openNav), addClass()"
      :class="{active: openNav}">
        <svg
          class="header__btn-icon"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M3 8V6H21V8H3ZM3 13H21V11H3V13ZM3 18H21V16H3V18Z"
            fill="#263640"
          />
        </svg>
      </button>
      <Transition name="header__navmenu">
        <div class="header__navmenu" v-show="size <= 992 ? openNav : true">
          <div class="header__navbar">
            <ul class="header__list">
              <li class="header__link">
                <router-link to="/" class="header__link-text" @click="closeNav"
                  >Asosiy&nbsp;sahifa</router-link
                >
              </li>
              <li class="header__link header__link_has_children">
                <span class="header__link-text">Biz</span>
                <ul class="header__sublist">
                  <li>
                    <router-link
                      to="/team"
                      class="header__sublink"
                      @click="closeNav"
                      >Jamoamiz</router-link
                    >
                    <router-link
                      to="/client"
                      class="header__sublink"
                      @click="closeNav"
                      >Mijozlarimiz</router-link
                    >
                  </li>
                </ul>
              </li>
              <li class="header__link header__link_has_children">
                <span class="header__link-text">Xizmatlar</span>
                <ul class="header__sublist">
                  <li>
                    <router-link
                      to="/services/manual-therapy"
                      class="header__sublink"
                      @click="closeNav"
                      >Manual terapiya</router-link
                    >
                  </li>
                  <li>
                    <router-link
                      to="/services/yumeyxo-therapy"
                      class="header__sublink"
                      @click="closeNav"
                      >Yumeyxo terapiya</router-link
                    >
                  </li>
                  <li>
                    <router-link
                      to="/services/al-hijam-therapy"
                      class="header__sublink"
                      @click="closeNav"
                      >Al-hijam</router-link
                    >
                  </li>
                </ul>
              </li>
            </ul>
            <button class="header__mail" @click="$emit('openModal', true)">
              <span>Bog'lanish</span>
              <svg
                width="18"
                height="16"
                viewBox="0 0 18 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  clip-rule="evenodd"
                  d="M16.2 0.800018H1.80003H0.0090332V15.2H1.80003H16.2H18V0.800018H16.2ZM1.80003 4.40002L9.00003 8.90002L16.2 4.40002V13.4H1.80003V4.40002ZM1.80003 2.60002L9.00003 7.10002L16.2 2.60002H1.80003Z"
                  fill="white"
                />
              </svg>
            </button>
          </div>
        </div>
      </Transition>
    </nav>
  </header>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";

export default {
  data() {
    return {
      openNav: false,
    };
  },
  computed: {
    ...mapState(["size"]),
  },

  methods: {
    ...mapActions(["resize"]),
    addClass() {
      this.$store.commit("addClass", this.size <= 992 ? this.openNav : false);
    },
    closeNav() {
      setTimeout(() => {
        this.openNav = false;
      }, 1000);
      this.$store.commit("addClass", false);
    },
  },

  mounted() {
    this.resize();
  },
};
</script>

<style>
</style>