import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router';

import Home from '@/pages/Home.vue'

const routes = createRouter({
  history: createWebHashHistory(),

  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/services/manual-therapy',
      name: 'manual-therapy',
      component: () => import('@/pages/Manual-therapy')
    },
    {
      path: '/services/yumeyxo-therapy',
      name: 'yumeyxo-therapy',
      component: () => import('@/pages/Yumeyxo-therapy.vue')
    },
    {
      path: '/services/al-hijam-therapy',
      name: 'hijam-therapy',
      component: () => import('@/pages/Hijam-therapy.vue')
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('@/pages/About.vue')
    },
    {
      path: '/contact',
      name: 'contact',
      component: () => import('@/pages/Contact.vue')
    },
    {
      path: '/team',
      name: 'team',
      component: () => import('@/pages/Team.vue')
    },
    {
      path: '/client',
      name: 'client',
      component: () => import('@/pages/Client.vue')
    }

  ],

  scrollBehavior(to, from, savedPosition) {
    return { 
      top: 0,
      behavior: 'smooth',
    }
  },
})

export default routes